:root {
  --font-body: Kumbh Sans, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --darkGray: hsl(0, 0%, 63%);
  --veryDarkGray: hsl(0, 0%, 27%);
  --verydarkdesaturatedblue: hsl(238, 29%, 16%);
  --softorange: hsl(14, 88%, 65%);
  --verydarkgrayishblue: hsl(237, 12%, 33%);
  --darkgrayishblue: hsl(240, 6%, 50%);
  --softviolet: hsl(273, 75%, 66%);
  --softblue: hsl(240, 73%, 65%);
  --lightgrayishblue: hsl(240, 5%, 91%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  overflow: scroll !important;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 0.8125rem;
  font-family: var(--font-body);
  color: var(--verydarkdesaturatedblue);
  background-repeat: no-repeat;
  background-image: linear-gradient(to bottom right, var(--softviolet), var(--softblue));
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 220vh;
}
@media (max-width: 26.66em) {
  body {
    background-image: linear-gradient(var(--softviolet), var(--softblue));
    contain: content;
  }
}
@media (max-width: 49.1em) {
  body {
    height: 130vh !important;
  }
}
@media (min-width: 62em) {
  body {
    height: 100vh !important;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin-bottom: 0 !important;
  text-align: left;
}
@media (max-width: 49.1em) {
  p {
    font-size: 0.9375rem;
  }
}

.container {
  padding: 0 1.5rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (min-width: 62em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

@media (max-width: 23.6em) {
  .illustration__img {
    max-height: 10.3125rem !important;
    position: relative;
    transform: translateY(-5.3125rem) !important;
  }

  .illustration__shadow {
    max-width: 12.5rem !important;
    position: relative;
    transform: translateY(-10.25rem) !important;
    left: 0.75rem !important;
  }

  p {
    font-size: 0.8125rem;
  }

  .accordion__answer {
    font: small-caption;
    font-size: 0.75rem !important;
  }
}
@media (min-width: 62em) {
  .a-card {
    background-repeat: no-repeat !important;
    background-image: url(/images/bg-pattern-desktop.svg), url(/images/illustration-woman-online-desktop.svg) !important;
    background-position: -5rem !important;
  }

  .illustration {
    height: 100vh !important;
  }

  .illustration__img {
    display: none;
  }

  .illustration__shadow {
    display: none;
  }

  .illustration__box {
    display: block !important;
    position: absolute !important;
    animation: box 0.8s cubic-bezier(0.15, 0.64, 0.63, 0.93) infinite alternate !important;
    left: -3.75rem;
    top: 9.6875rem;
  }

  @keyframes box {
    0% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(-50px);
    }
  }
  .a-card {
    display: grid;
    grid-template-columns: 3fr 2.143fr;
  }
}
.container {
  padding-left: 1.875rem !important;
  padding-right: 1.875rem !important;
}
@media (max-width: 49.1em) {
  .container {
    max-width: 30.625rem !important;
  }
}
@media (min-width: 62em) {
  .container {
    max-width: 56.25rem !important;
  }
}

.a-card {
  background: var(--white);
  border-radius: 2rem;
  width: 100%;
}

.illustration {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 1.875rem;
  max-height: 7.5rem;
}
.illustration__box {
  display: none;
}
.illustration__img {
  max-height: 12.5rem;
  position: relative;
  transform: translateY(-6.25rem);
}
.illustration__shadow {
  max-height: 13.75rem;
  position: relative;
  transform: translateY(-11.875rem);
  left: 0.9375rem;
}

.faq-container h1 {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 2.1875rem;
  font-weight: 700;
  text-align: center;
}

.accordion {
  position: relative;
  padding: 0.3125rem 1.5625rem 1.5625rem 1.5625rem;
}

.accordion__item {
  padding-top: 0.5rem;
  border-bottom: 0.0625rem solid var(--lightgrayishblue);
}

.accordion__item h2 {
  margin-bottom: 0 !important;
}

.accordion__question {
  width: 100%;
  border: none;
  background: transparent;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 0.9375rem;
  font-family: var(--font-body);
  color: var(--verydarkdesaturatedblue);
}

.accordion__question::after {
  float: right;
  content: "";
  width: 0.625rem;
  height: 0.625rem;
  background-image: url(/images/icon-arrow-down.svg);
  background-repeat: no-repeat;
  background-size: contain;
  transition: transform 0.2s ease-in-out;
}

.accordion__answer {
  font-size: 0.8125rem;
  font-family: var(--font-body);
  line-height: 1.375rem;
  color: var(--verydarkgrayishblue);
  margin-bottom: 1rem;
}
@media (max-width: 49.1em) {
  .accordion__answer {
    margin-right: 1rem !important;
    padding-right: 1rem !important;
    font-size: 0.8875rem;
  }
}

.accordion__collapse:not(.show) {
  display: none;
}

.accordion__question:not(.collapsed)::after {
  transform: rotate(-180deg);
  position: static;
}

.collapse:not(.show) {
  display: none;
}

.accordion__question:not(.collapsed) {
  font-weight: bold;
  color: var(--softorange);
}